import Link from '@tiptap/extension-link';

/**
 * CustomLink -  Tiptap Link  title 
 * 
 * Tiptap  Link  href  target
 *  title 
 * 
 * Markdown : [text](url "title")
 * HTML : <a href="url" title="title">text</a>
 */
export const CustomLink = Link.extend({
    addAttributes() {
        return {
            // 
            ...this.parent?.(),
            //  title 
            title: {
                default: null,
                parseHTML: (element) => element.getAttribute('title'),
                renderHTML: (attributes) => {
                    if (!attributes.title) {
                        return {};
                    }
                    return { title: attributes.title };
                },
            },
        };
    },

    // Markdown  token  title
    parseMarkdown: (token: any, helpers: any) => {
        const content = helpers.parseInline(token.tokens || []);
        return helpers.applyMark('link', content, {
            href: token.href || '',
            title: token.title || null,
        });
    },

    // Markdown  title 
    // Marks  helpers.renderChildren(node)  node.content
    // 
    renderMarkdown: (node: any, helpers: any) => {
        const content = helpers.renderChildren(node);
        const href = node.attrs?.href || '';
        const title = node.attrs?.title;
        
        if (title) {
            return `[${content}](${href} "${title}")`;
        }
        return `[${content}](${href})`;
    },
});

export default CustomLink;
