import { Mark, mergeAttributes } from '@tiptap/core';
import { getLbInlineTokens } from './markedHelper';

export interface TextSizeOptions {
    HTMLAttributes: Record<string, any>;
}

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        textSize: {
            setTextSize: (size: number) => ReturnType;
            unsetTextSize: () => ReturnType;
        };
    }
}

const SIZE_REGEX = /^\[size=(\d+)\]([\s\S]*?)\[\/size\]/;

export const TextSize = Mark.create<TextSizeOptions>({
    name: 'textSize',

    addOptions() {
        return {
            HTMLAttributes: {},
        };
    },

    addAttributes() {
        return {
            size: {
                default: null,
                parseHTML: (element) => {
                    const fontSize = element.style.fontSize;
                    if (!fontSize) return null;
                    return parseInt(fontSize, 10) || null;
                },
                renderHTML: (attributes) => {
                    if (!attributes.size) return {};
                    return { style: `font-size: ${attributes.size}px` };
                },
            },
        };
    },

    parseHTML() {
        return [
            {
                tag: 'span[style*="font-size"]',
                getAttrs: (element) => {
                    const el = element as HTMLElement;
                    const fontSize = el.style.fontSize;
                    if (!fontSize) return false;
                    const size = parseInt(fontSize, 10);
                    if (!size) return false;
                    return { size };
                },
            },
        ];
    },

    renderHTML({ HTMLAttributes }) {
        return ['span', mergeAttributes(this.options.HTMLAttributes, HTMLAttributes), 0];
    },

    addCommands() {
        return {
            setTextSize: (size: number) => ({ commands }) => {
                return commands.setMark(this.name, { size });
            },
            unsetTextSize: () => ({ commands }) => {
                return commands.unsetMark(this.name);
            },
        };
    },

    markdownTokenName: 'text_size',

    markdownTokenizer: {
        name: 'text_size',
        level: 'inline',
        start: (src: string) => src.indexOf('[size='),
        tokenize: (src: string, tokens: any[], lexer: any) => {
            const match = SIZE_REGEX.exec(src);
            if (!match) return undefined;
            return {
                type: 'text_size',
                raw: match[0],
                size: parseInt(match[1], 10),
                text: match[2],
                tokens: getLbInlineTokens(match[2]),
            };
        },
    },

    parseMarkdown: (token: any, helpers: any) => {
        const content = helpers.parseInline(token.tokens || []);
        return helpers.applyMark('textSize', content, { size: token.size });
    },

    renderMarkdown: (node: any, helpers: any) => {
        const content = helpers.renderChildren(node);
        const size = node.attrs?.size || 16;
        return `[size=${size}]${content}[/size]`;
    },
});

export default TextSize;
