import { Node } from '@tiptap/core';
import { getLbInlineTokens } from './markedHelper';

export const SpoilerInlineParagraph = Node.create({
    name: 'spoilerInlineParagraph',

    markdownTokenName: 'spoiler_inline_paragraph',

    markdownTokenizer: {
        name: 'spoiler_inline_paragraph',
        level: 'block',
        start: (src: string) => {
            const match = /^>![^\s]/.exec(src);
            return match ? 0 : -1;
        },
        tokenize: (src: string, tokens: any[], lexer: any) => {
            const lineMatch = /^(.*?)(?:\n|$)/.exec(src);
            if (!lineMatch) return undefined;
            
            const raw = lineMatch[0];
            const line = raw.replace(/\n$/, '');
            
            //  block spoiler">! " 
            if (/^>! /.test(line)) return undefined;
            
            //  spoiler 
            if (!/^>![^!]+!</.test(line) && !/\|\|[^|]+\|\|/.test(line)) return undefined;
            
            //  vs spoiler 
            //  getLbInlineTokens  lexer
            const mixed: any[] = [];
            const re = />!([^!]+)!<|\|\|([^|]+)\|\|/g;
            let last = 0;
            let m: RegExpExecArray | null;
            
            while ((m = re.exec(line)) !== null) {
                // 
                if (m.index > last) {
                    mixed.push(...getLbInlineTokens(line.slice(last, m.index)));
                }
                
                // spoiler 
                const inner = m[1] ?? m[2] ?? '';
                mixed.push({
                    type: 'spoiler_inline',
                    raw: m[0],
                    text: inner,
                    tokens: getLbInlineTokens(inner),
                });
                
                last = m.index + m[0].length;
            }
            
            // 
            if (last < line.length) {
                mixed.push(...getLbInlineTokens(line.slice(last)));
            }
            
            return {
                type: 'spoiler_inline_paragraph',
                raw,
                tokens: mixed,
            };
        },
    },

    parseMarkdown: (token: any, helpers: any) => {
        const content = helpers.parseInline(token.tokens || []);
        return { 
            type: 'paragraph', 
            content: content.length ? content : undefined 
        };
    },
});

export default SpoilerInlineParagraph;
