import { Node } from '@tiptap/core';

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        blankLine: {
            insertBlankLine: () => ReturnType;
        };
    }
}

// 
const LB_BLANK_REGEX = /^\[lb-blank\](?:[^\[]*)\[\/lb-blank\]/;

/**
 * BlankLine - 
 * 
 *  Markdown  Markdown 
 * 
 * Markdown : [lb-blank][/lb-blank]
 * HTML : <div class="lb-blank"></div>
 */
export const BlankLine = Node.create({
    name: 'blankLine',

    group: 'block',

    atom: true,  // /

    selectable: true,

    draggable: true,

    parseHTML() {
        return [
            { tag: 'div.lb-blank' },
            { tag: 'span.lb-blank' },  //  more-format 
        ];
    },

    renderHTML() {
        return ['div', { class: 'lb-blank' }];
    },

    addCommands() {
        return {
            insertBlankLine: () => ({ chain, state }) => {
                const { selection } = state;
                
                //  blankLine 
                if (selection.node?.type.name === 'blankLine') {
                    return chain()
                        .setTextSelection(selection.to)
                        .insertContent([
                            { type: 'blankLine' },
                            { type: 'blankLine' },
                        ])
                        .run();
                }
                
                //  blankLine 
                return chain()
                    .insertContent([
                        { type: 'blankLine' },
                        { type: 'blankLine' },
                    ])
                    .run();
            },
        };
    },

    addKeyboardShortcuts() {
        return {
            // Mod+Shift+Enter 
            'Mod-Shift-Enter': () => this.editor.commands.insertBlankLine(),
        };
    },

    // Markdown token 
    markdownTokenName: 'lb_blank',

    // Markdown tokenizer:  [lb-blank][/lb-blank]
    markdownTokenizer: {
        name: 'lb_blank',
        level: 'block',
        start: (src: string) => src.indexOf('[lb-blank]'),
        tokenize: (src: string) => {
            const match = LB_BLANK_REGEX.exec(src);
            if (!match) return undefined;
            return {
                type: 'lb_blank',
                raw: match[0],
            };
        },
    },

    // Token  Tiptap JSON
    parseMarkdown: (token: any, helpers: any) => {
        return { type: 'blankLine' };
    },

    // Tiptap JSON  Markdown
    //  1 
    renderMarkdown: (node: any, helpers: any) => {
        return '[lb-blank][/lb-blank]\n';
    },
});

export default BlankLine;
